$prefix: ".wc-booster";

#{$prefix}-quick-view {
	cursor: pointer;
	position: relative;
	/*padding: 5px 10px !important;*/
	line-height: normal !important;
	.quick-view-text,
	.quick-view-text-left {
		position: absolute;
		top: 6px;
		right: -96px;
		visibility: hidden;
		opacity: 0;
		transition: all 0.5s ease-in-out;
		transform: translateX(20px);
		span {
			padding: 5px 10px;
			background-color: #2a2a2a7c;
			color: #fff;
			position: relative;
			font-size: 14px;
			&:before {
				content: "";
				position: absolute;
				left: -5px;
				top: 50%;
				width: 11px;
				height: 11px;
				background-color:rgba(42, 42, 42, 0.4862745098);
				clip-path: polygon(0 50%, 100% 100%, 100% 0);
				transform: translate(-50%, -50%);
			}
		}
	}
	&:hover {
		.quick-view-text,
		.quick-view-text-left {
			transition: all 0.5s ease-in-out;
			transform: translateX(0);
		}
	}
	.quick-view-text-left {
		right: unset;
		left: -96px;
		transform: translateX(-20px);
		span {
			&:before {
				left: unset;
				right: -16px;
				top: 50%;
				clip-path: polygon(100% 50%, 0 100%, 0 0);
				transform: translate(-50%, -50%);
			}
		}
	}
}

.single-product {
	.wp-block-column {
		position: relative;
	}
}

.wp-block-woocommerce-product-collection .wc-block-product {
	position: relative;
}

